"use client";
import { useEffect, useState } from "react";
import { useParams, useRouter } from "next/navigation";
import { useLoginClient } from "../../context/regester/login_context";
import { useApi } from "../../context/ApiContext";
import { useToken } from "../../context/token_context";
import Image from "next/image";

export default function PortfolioDetailsPage() {
  const { id } = useParams();
  const router = useRouter();
  const { token, saveData } = useLoginClient();
  const { userData } = useToken();
  const { baseUrl } = useApi();

  const [portfolio, setPortfolio] = useState<any>(null);
  const [loading, setLoading] = useState(true);
  const [editName, setEditName] = useState("");
  const [editDescription, setEditDescription] = useState("");

  const userRole =
    userData?.["http://schemas.microsoft.com/ws/2008/06/identity/claims/role"] || "";
  const isOwnProfile = userRole === "Worker" && portfolio?.workerId === saveData?.workerId;

  useEffect(() => {
    if (!token || !id) return;

    const fetchDetails = async () => {
      try {
        const res = await fetch(`${baseUrl}/api/Portfolio/${id}`, {
          headers: { Authorization: `Bearer ${token}` },
        });

        if (!res.ok) throw new Error("ŸÅÿ¥ŸÑ ŸÅŸä ÿ¨ŸÑÿ® ÿ™ŸÅÿßÿµŸäŸÑ ÿßŸÑÿ®Ÿàÿ±ÿ™ŸÅŸàŸÑŸäŸà");

        const data = await res.json();
        const imageUrls = Array.isArray(data.imageUrls)
          ? data.imageUrls.map((url: any, index: number) =>
              typeof url === "string" ? { id: index, imageUrl: url } : url
            )
          : [];

        setPortfolio({ ...data, imageUrls });
        setEditName(data.name);
        setEditDescription(data.description);
      } catch (err) {
        console.error("‚ùå Error fetching portfolio details:", err);
      } finally {
        setLoading(false);
      }
    };

    fetchDetails();
  }, [id, token]);

  const handleDeleteImage = async (imageId: number) => {
    if (!confirm("ŸáŸÑ ÿ™ÿ±ŸäÿØ ÿ≠ÿ∞ŸÅ Ÿáÿ∞Ÿá ÿßŸÑÿµŸàÿ±ÿ©ÿü")) return;

    try {
      const res = await fetch(`${baseUrl}/api/Portfolio/${id}/images/${imageId}`, {
        method: "DELETE",
        headers: { Authorization: `Bearer ${token}` },
      });

      if (!res.ok) throw new Error("ŸÅÿ¥ŸÑ ŸÅŸä ÿ≠ÿ∞ŸÅ ÿßŸÑÿµŸàÿ±ÿ©");

      setPortfolio((prev: any) => ({
        ...prev,
        imageUrls: prev.imageUrls.filter((img: any) => img.id !== imageId),
      }));
    } catch (err) {
      console.error("‚ùå Error deleting image:", err);
    }
  };

 const handleSaveChanges = async () => {
  try {
    const res = await fetch(`${baseUrl}/api/Portfolio/${id}`, {
      method: "PUT",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`,
      },
      body: JSON.stringify({
        name: editName,
        description: editDescription,
      }),
    });

    if (!res.ok) {
      const errorText = await res.text();
      console.error("‚ùå ÿ™ŸÅÿßÿµŸäŸÑ ÿßŸÑÿÆÿ∑ÿ£:", errorText);
      throw new Error("ŸÅÿ¥ŸÑ ŸÅŸä ÿ≠ŸÅÿ∏ ÿßŸÑÿ™ÿπÿØŸäŸÑÿßÿ™");
    }

    setPortfolio((prev: any) => ({
      ...prev,
      name: editName,
      description: editDescription,
    }));

    alert("‚úÖ ÿ™ŸÖ ÿ™ÿ≠ÿØŸäÿ´ ÿßŸÑÿ®ŸäÿßŸÜÿßÿ™ ÿ®ŸÜÿ¨ÿßÿ≠");
  } catch (err) {
    console.error("‚ùå Error updating portfolio:", err);
  }
};


  if (loading) return <div className="p-4">ÿ¨ÿßÿ±Ÿä ÿßŸÑÿ™ÿ≠ŸÖŸäŸÑ...</div>;
  if (!portfolio) return <div className="p-4 text-red-600">ŸÑŸÖ Ÿäÿ™ŸÖ ÿßŸÑÿπÿ´Ÿàÿ± ÿπŸÑŸâ ÿßŸÑÿ®Ÿàÿ±ÿ™ŸÅŸàŸÑŸäŸà</div>;

  return (
    <div className="p-4 bg-white min-h-screen">
      {isOwnProfile ? (
        <>
          <input
            type="text"
            value={editName}
            onChange={(e) => setEditName(e.target.value)}
            className="text-xl font-bold mb-2 text-green-700 border p-2 w-full rounded"
            placeholder="ÿßÿ≥ŸÖ ÿßŸÑÿ®Ÿàÿ±ÿ™ŸÅŸàŸÑŸäŸà"
          />
          <textarea
            value={editDescription}
            onChange={(e) => setEditDescription(e.target.value)}
            className="mb-4 w-full border p-2 rounded text-gray-600"
            placeholder="ÿßŸÑŸàÿµŸÅ"
          />
        </>
      ) : (
        <>
          <h1 className="text-xl font-bold mb-4 text-green-700">{portfolio.name}</h1>
          <p className="mb-4 text-gray-600">{portfolio.description}</p>
        </>
      )}

      <div className="grid grid-cols-2 md:grid-cols-3 gap-2 mb-6">
        {portfolio.imageUrls?.map((img: any) => (
          <div key={img.id} className="relative group">
            <Image
              src={img.imageUrl}
              alt="portfolio image"
              width={200}
              height={200}
              className="rounded object-cover w-full h-32"
            />
            {isOwnProfile && (
              <button
                onClick={() => handleDeleteImage(img.id)}
                className="absolute top-1 right-1 bg-white text-red-600 rounded-full p-1 text-xs opacity-0 group-hover:opacity-100 transition"
                title="ÿ≠ÿ∞ŸÅ ÿßŸÑÿµŸàÿ±ÿ©"
              >
                üóëÔ∏è
              </button>
            )}
          </div>
        ))}
      </div>

      {isOwnProfile && (
        <div className="flex flex-wrap gap-2 mb-6">
          <button
            onClick={handleSaveChanges}
            className="bg-green-600 text-white px-4 py-2 rounded"
          >
            üíæ ÿ≠ŸÅÿ∏ ÿßŸÑÿ™ÿπÿØŸäŸÑÿßÿ™
          </button>
          <button
            onClick={() => router.push(`/add-portfolio-images/${id}`)}
            className="bg-gray-600 text-white px-4 py-2 rounded"
          >
            ‚ûï ÿ•ÿ∂ÿßŸÅÿ© ÿµŸàÿ±
          </button>
        </div>
      )}

      <div>
        <button
          onClick={() => router.back()}
          className="bg-gray-300 px-4 py-2 rounded hover:bg-gray-400"
        >
          ÿßŸÑÿ±ÿ¨Ÿàÿπ
        </button>
      </div>
    </div>
  );
}
